import sweetalert2 from "sweetalert2";

export function stringifyAlertType(_arg1) {
    switch (_arg1.tag) {
        case 3: {
            return "error";
        }
        case 1: {
            return "question";
        }
        case 2: {
            return "warning";
        }
        case 4: {
            return "success";
        }
        default: {
            return "info";
        }
    }
}

export function stringifyPosition(_arg1) {
    switch (_arg1.tag) {
        case 8: {
            return "bottom-end";
        }
        case 7: {
            return "bottom-start";
        }
        case 0: {
            return "top";
        }
        case 2: {
            return "top-end";
        }
        case 1: {
            return "top-start";
        }
        case 3: {
            return "center";
        }
        case 5: {
            return "center-end";
        }
        case 4: {
            return "center-start";
        }
        default: {
            return "bottom";
        }
    }
}

export function stringifyInputType(_arg1) {
    switch (_arg1.tag) {
        case 1: {
            return "password";
        }
        case 2: {
            return "textarea";
        }
        default: {
            return "text";
        }
    }
}

export const swal = sweetalert2;

export function fire(x) {
    return swal.fire(x);
}

